@include body-class(true) {
  .cross-sell-plugins--free {
    .cross-sell-plugin-card-description {
      color: #666;
    }

    .cross-sell-card {
      &:hover,
      &:focus {
        border-color: #aaa;
        box-shadow:
          0px 2px 4px 0px #7373731a,
          0px 1px 16px 0px #7373730d,
          0px 6px 12px 0px #73737314;
      }
    }
  }

  .cross-sell-plugin-card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .cross-sell-plugin-card-content {
    display: flex;
    flex-direction: column;
    gap: 10px;

    h2 {
      font-weight: 600;
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 0px;
      color: #101010;
    }
  }

  .cross-sell-plugin-card-rating {
    display: flex;
    gap: 15px;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    letter-spacing: 0px;
    color: #888888;

    &__stars {
      display: flex;
      align-items: center;
      gap: 5px;
    }
  }

  .cross-sell-plugin-card-description {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0px;
    color: #333;
  }

  .cross-sell-plugin-card-features {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .cross-sell-plugin-card-feature {
    display: flex;
    gap: 8px;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0px;
    color: #666666;
  }
}
