@include body-class(true) {
  .cross-sell-features {
    gap: 15px;

    .cross-sell-feature-header {
      display: flex;
      align-items: center;
      gap: 10px;
    }

    h3 {
      font-size: 20px;
      line-height: 30px;
    }

    .cross-sell-feature-arrow {
      margin-left: auto;
    }

    .cross-sell-card {
      box-shadow: unset;
      padding: 30px;
    }

    p {
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
      line-height: 24px;
      margin: 0;
    }
  }
}
