@import "./shared-ui.scss";
@import "./hero.scss";
@import "./highlights.scss";
@import "./section.scss";
@import "./plugin-card.scss";
@import "./services.scss";
@import "./features.scss";
@import "./footer.scss";

@include body-class(true) {
  * {
    box-sizing: border-box;
  }

  &.sui-wrap:not(.sui-modal) {
    margin-top: 0;
  }

  h1,
  h2,
  h3 {
    margin: 0;
  }

  .cross-sell-text-center {
    text-align: center;
  }

  p {
    font-size: 16px;
    line-height: 30px;
    letter-spacing: -0.5px;
  }

  .sui-tabs {
    &:not(.sui-side-tabs) {
      & > .sui-tabs-menu {
        .sui-tab-item {
          font-size: 15px;
        }
      }
    }
  }

  .wpmudev-cross-sell-page {
    background: #fff;
    margin-left: -30px;
    margin-right: -30px;

    @include media(max-width, md) {
      margin-left: -10px;
      margin-right: -10px;
    }
  }

  .cross-sell-wrapper {
    max-width: 1180px;
    padding-left: 50px;
    padding-right: 50px;
    margin-left: auto;
    margin-right: auto;

    @include media(max-width, md) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  .cross-sell-flushed {
    border-top: 1px solid #e6e6e6;

    &.sui-tabs:not(.sui-side-tabs) {
      &.sui-tabs-flushed {
        margin: 0 !important;
      }

      .sui-tabs-content {
        .sui-tab-content {
          padding: 0;
        }
      }

      &.sui-tabs-overflow > .sui-tabs-menu {
        margin: 0 auto;
        padding-left: 50px;
        padding-right: 50px;

        @include media(max-width, md) {
          padding-left: 30px;
          padding-right: 30px;
        }
      }
    }
  }

  .cross-sell-section,
  .cross-sell-horizontal-line,
  .cross-sell-highlights {
    margin-top: 100px;
    margin-bottom: 100px;

    @include media(max-width, lg) {
      margin-top: 80px;
      margin-bottom: 80px;
    }

    @include media(max-width, md) {
      margin-top: 60px;
      margin-bottom: 60px;
    }
  }

  .cross-sell-horizontal-line {
    background: #d9d9d9;
  }

  .cross-sell-link {
    font-weight: 400;
    color: #0059ff;
    text-decoration: underline;

    &:hover:not(.sui-button),
    &:focus:not(.sui-button) {
      color: #0045c4;
    }
  }

  .cross-sell-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 30px;
  }

  .cross-sell-card {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 25px;
    border-radius: 10px;
    border: 1px solid #e6e6e6;
    background: #ffffff;
    box-shadow:
      0px 2px 1px 0px #7373730d,
      0px 1px 2px 0px #7373730d;

    .cross-sell-card-arrow-alt {
      fill: #aaa;
    }

    .cross-sell-feature-arrow {
      fill: #888;
    }

    &:hover,
    &:focus {
      border-color: #aaa;
      box-shadow:
        0px 1px 2px 0px #7373730d,
        0px 2px 1px 0px #7373730d;

      .cross-sell-feature-arrow,
      .cross-sell-card-arrow-alt {
        fill: #333;
      }
    }
  }

  //   Site management
  .cross-sell-site {
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
  }

  // button
  .cross-sell-button {
    &:not(.sui-button) {
      font-weight: 500;
      font-size: 15px;
      line-height: 24px;
      letter-spacing: 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px 20px;
      border-radius: 5px;
      background: #0059ff;
      color: #fff;
      cursor: pointer;
      transition: background 0.3s;

      &:hover {
        background: #0047cc;
        color: #fff;
      }

      &:focus {
        outline: 2px solid #0059ff;
        outline-offset: 2px;
        background: #0045c4;
        color: #fff;
      }
    }
  }

  .cross-sell-highlighted-price {
    display: inline-block;
    margin-left: 4px;
    text-decoration: line-through;
    opacity: 0.5;
  }

  //   membership
  .cross-sell-section-membership {
    gap: 30px;

    h2 {
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      letter-spacing: -0.5px;
      color: #101010;
    }

    .cross-sell-section-header-wrapper {
      gap: 15px;
    }

    .cross-sell-section-description {
      position: relative;
      font-size: 20px;
      line-height: 32px;
      letter-spacing: -0.5px;

      &::before {
        content: "";
        position: absolute;
        background: url("data:image/svg+xml;base64,")
          no-repeat;
        width: 110px;
        height: 95px;
        right: -45px;

        @include media(max-width, md) {
          content: unset;
        }
      }
    }
  }
}

#wpbody-content {
  padding-bottom: 0 !important;
}
