@include body-class(true) {
  .cross-sell-highlights {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
    padding: 20px 0;
    background: #f8f8f8;
    border-radius: 10px;

    @include media(max-width, md) {
      padding-left: 30px;
      padding-right: 30px;
    }

    .cross-sell-highlight {
      display: flex;
      flex: 1 1 340px;
      flex-direction: column;
      align-items: center;
      gap: 8px;
      max-width: 100%;
    }

    .cross-sell-highlight__title {
      font-weight: 600;
      font-size: 16px;
      line-height: 30px;
      letter-spacing: 0px;
      text-align: center;
      color: #333;
    }
  }
}
