@include body-class(true) {
  .cross-sell-section {
    display: flex;
    gap: 50px;
    flex-direction: column;
    justify-content: center;
    margin: 100px 0;
  }

  .cross-sell-section-header {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
  }

  .cross-sell-section-title-underline {
    position: relative;
    display: inline-block;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      bottom: -100%;
      position: absolute;
      background: url("data:image/svg+xml;base64,")
        no-repeat;
    }
  }

  .cross-sell-section-description {
    font-size: 16px;
    line-height: 30px;
    letter-spacing: -0.5px;
    text-align: center;
  }

  .cross-sell-section-header-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .cross-sell-section--services {
    gap: 30px;
  }
}
