@include body-class(true) {
  .cross-sell-hero {
    background: linear-gradient(126.64deg, #ffffff 14.27%, #d9e6fe 234.25%);

    &__wrapper {
      display: flex;
      gap: 30px;
      justify-content: space-between;
      align-items: center;
      padding: 50px 0;

      @include media(max-width, md) {
        flex-direction: column;
        align-items: flex-start;
        gap: 30px;
      }
    }

    &__image {
      order: 1;

      @include media(max-width, md) {
        order: unset;
      }

      img {
        width: 100%;
      }
    }

    &__content {
      max-width: 500px;

      h2 {
        font-weight: 700;
        font-size: 32px;
        line-height: 40px;
        letter-spacing: -0.5px;
        color: #101010;

        @include media(max-width, lg) {
          font-size: 24px;
        }

        @include media(max-width, md) {
          font-size: 22px;
        }
      }

      p {
        font-size: 18px;
        line-height: 30px;
      }
    }
  }
}
