@include body-class(true) {
  .cross-sell-section--services {
    .cross-sell-services-cards {
      grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
      gap: 30px;

      .cross-sell-card {
        align-items: center;
        justify-content: center;
        box-shadow: unset;
        flex: 1;
        padding: 25px 20px;
        gap: 15px;
        border-radius: 15px;

        h3 {
          line-height: 30px;
          max-width: 140px;
          text-align: center;
        }

        p {
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
          overflow: hidden;
          font-size: 15px;
          line-height: 24px;
          text-align: center;
          margin: 0;
        }
      }
    }
  }
}
